@import "constants/breaks";
@import "mixins/break";
@import "./constants/colors";

// .Page-body {
//   margin-top: 60px;
// }
.HomePage {
  .spotlight {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    // overflow: hidden;
    // padding-bottom: 90vh;
    height: 90vw;
    max-height: 90vh;
    min-height: 50vh;

    .bd-dark {
      position: absolute;
      background-color: #000;
      width: 100%;
      height: 90vw;
      max-height: 90vh;
      min-height: 50vh;
      z-index: 0;
    }
    figure {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 100%;
      margin: 0;
      .spotlight-gradient {
        position: absolute;
        background-color: #0000005e;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        z-index: 0;
        // background-image: url(../static/assets/spotlight_gradient.png);
        background-position: -40px 0px;
        background-repeat: no-repeat;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        background-size: cover;
        z-index: 1;
      }
      .lazy {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
      }
    }
  }
  .backgroud-steps {
    width: 100%;
    // height: 90vw;
    // min-height: 38vh;
    background-image: -moz-radial-gradient(
      circle at top center,
      $color-gradient1 45%,
      $color-gradient2 100%
    ); /* FF3.6+ */
    background-image: -webkit-radial-gradient(
      circle at top center,
      $color-gradient1 45%,
      $color-gradient2 100%
    ); /* Chrome10+,Safari5.1+ */
    background-image: -o-radial-gradient(
      circle at top center,
      $color-gradient1 45%,
      $color-gradient2 100%
    ); /* Opera 11.10+ */
    background-image: -ms-radial-gradient(
      circle at top center,
      $color-gradient1 45%,
      $color-gradient2 100%
    ); /* IE10+ */
    background-image: radial-gradient(
      circle at top center,
      $color-gradient1 45%,
      $color-gradient2 100%
    ); /* W3C */
  }
  .steps {
    padding: 40px 0px;
    max-width: 1200px;
    margin: auto;
    .step_name {
      color: $color-white;
      font-size: 18px;
      text-align: center;
    }
    .step_number {
      color: $color-white;
      background: $color-secondary;
      width: 60px;
      text-align: center;
      border-radius: 40px 40px 0 0;
      margin: 20px auto 5px auto;
      padding: 6px 0px;
      font-size: 20px;
    }
    .step_body {
      position: relative;
      width: 200px;
      margin: 0px auto;
    }

    .step_name:after {
      content: "";
      background: #dc2d76;
      width: 60px;
      text-align: center;
      border-radius: 0 0 40px 40px;
      margin: 10px auto 0px auto;
      height: 35px;
      position: absolute;
      left: 0;
      right: 0;
    }
    .step_icon {
      width: 130px;
      height: 130px;
      background-size: cover;
      margin: auto;
      margin-top: 20px;
    }
  }
}
